.container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #1B1A1F;
    height: 50px;
    position: relative;
}

.navigationDiv {
    display: flex;
    position: relative;
    align-items: center;
    margin-left: 20px;
}

.searchDiv {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    
}
.navButton {
    color: white;
    margin-left: 15px;
    text-decoration: none;
    margin-right: 15px;
}

.navButton:hover {
    cursor: pointer;
    color: #0077b6;
}

.createButton {
    color: #939699;
    margin-left: 15px;
    text-decoration: none;
    margin-right: 15px;
    border: 1px solid #939699;
    border-radius: 20px;
}

.createButton .text {
    color: white;
    padding: 5px;
    padding-left: 20px;
    padding-right:20px;
    margin: 0;
    border-radius: 20px;
    white-space: nowrap;
}

.createButton .text:hover {
    background-color: #219ebc;
}



.searchText {
    border-radius: 15px;
    text-align: center;
    height: 37px;
    width: 60%;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    border: none; 
    background-color: #ffff;
}

.searchText:focus {
    outline: none;
}

.searchButton {
    border-radius: 15px;
    height: 39px;
    border: none;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    align-items: center;
    background-color: #ffff;
    width: 40px;
}

.searchButton:hover {
    cursor: pointer;
}

.accountActions {
    display: flex;
    justify-content: center;
    align-items: center;
}

.imgSearch {
    width: 20px;
}

.profileIcon {
    width: 38px;
    height: 38px;
    border-radius: 100%;
    margin-right: 15px;

}

.profileIcon:hover {
    cursor: pointer;
}

.img404NotFoundDog {
    margin-left: -70px;
}

.modalMessage {
    color: #0077b6
}

.accountWindow {
    position: absolute;
    width: 200px;
    height: auto;
    right: 5px;
    top: 60px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-radius: 10px;
    background-color: #1B1A1F;
    z-index: 1;
    text-decoration: none;
}

.accountWindow p {
    color: white;
    margin: 0;
    padding-top: 10px;
    padding-bottom: 10px;
}

.navAccount {
    text-decoration: none;
}

.accountWindow p:hover {
    color: #219ebc;
    cursor: pointer;
    border-radius: 20px;
}

.vineta {
    width: 20px; 
     height: 20px;  
     background: #1B1A1F;
     position: absolute;
     right: 25px;
     top: 55px;
     z-index: 3;
     -webkit-transform: rotate(45deg);
     -moz-transform: rotate(45deg);
     -ms-transform: rotate(45deg);
     -o-transform: rotate(45deg);
     transform: rotate(45deg);

}